"use client";

import { useState, useEffect, Suspense, useRef } from "react";
import { useSearchParams } from "next/navigation";
import Header from "@/components/Header";
import { getProductById } from "@/lib/products";
import { Product, Design, DesignElement, DesignFace } from "@/lib/types";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import Link from "next/link";

function EditorContent() {
  const searchParams = useSearchParams();
  const productId = searchParams.get("product");
  const fileInputRef = useRef<HTMLInputElement>(null);
  
  const [product, setProduct] = useState<Product | null>(null);
  const [currentFace, setCurrentFace] = useState<'A' | 'B'>('A');
  const [design, setDesign] = useState<Design | null>(null);
  const [selectedElement, setSelectedElement] = useState<string | null>(null);
  const [selectedSize, setSelectedSize] = useState<string>("");
  
  // Text editing states
  const [newText, setNewText] = useState("");
  const [textColor, setTextColor] = useState("#000000");
  const [fontSize, setFontSize] = useState(16);
  
  // Element manipulation states
  const [isDragging, setIsDragging] = useState(false);
  const [isResizing, setIsResizing] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });

  useEffect(() => {
    if (productId) {
      const foundProduct = getProductById(productId);
      if (foundProduct) {
        setProduct(foundProduct);
        setSelectedSize(foundProduct.sizes[0]?.label || "");
        // Initialize design
        const initialDesign: Design = {
          id: `design-${Date.now()}`,
          productId: foundProduct.id,
          faceA: { elements: [], backgroundColor: "#FFFFFF" },
          faceB: foundProduct.faces === 2 ? { elements: [], backgroundColor: "#FFFFFF" } : undefined,
          createdAt: new Date(),
          updatedAt: new Date()
        };
        setDesign(initialDesign);
      }
    }
  }, [productId]);

  const addTextElement = () => {
    if (!design || !newText.trim()) return;
    
    const newElement: DesignElement = {
      id: `text-${Date.now()}`,
      type: 'text',
      content: newText,
      position: { x: 50, y: 50 },
      size: { width: 200, height: 30 },
      rotation: 0,
      color: textColor,
      fontSize: fontSize,
      fontFamily: 'Inter'
    };

    const updatedDesign = { ...design };
    if (currentFace === 'A') {
      updatedDesign.faceA.elements.push(newElement);
    } else if (updatedDesign.faceB) {
      updatedDesign.faceB.elements.push(newElement);
    }
    
    setDesign(updatedDesign);
    setNewText("");
  };

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file && file.type.startsWith('image/')) {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (!design) return;
        
        const canvasSize = getCanvasSize();
        const maxWidth = canvasSize.width - 40; // Margen de 20px a cada lado
        const maxHeight = canvasSize.height - 40; // Margen de 20px arriba y abajo
        
        // Crear imagen temporal para obtener dimensiones originales
        const img = new Image();
        img.onload = () => {
          let newWidth = img.width;
          let newHeight = img.height;
          
          // Calcular escala para que la imagen se expanda pero mantenga proporción
          const scaleX = maxWidth / img.width;
          const scaleY = maxHeight / img.height;
          const scale = Math.min(scaleX, scaleY, 2); // Máximo 2x de escala
          
          newWidth = img.width * scale;
          newHeight = img.height * scale;
          
          // Centrar la imagen
          const x = (canvasSize.width - newWidth) / 2;
          const y = (canvasSize.height - newHeight) / 2;
          
          const newElement: DesignElement = {
            id: `image-${Date.now()}`,
            type: 'image',
            content: e.target?.result as string,
            position: { x: Math.max(20, x), y: Math.max(20, y) },
            size: { width: newWidth, height: newHeight },
            rotation: 0
          };

          const updatedDesign = { ...design };
          if (currentFace === 'A') {
            updatedDesign.faceA.elements.push(newElement);
          } else if (updatedDesign.faceB) {
            updatedDesign.faceB.elements.push(newElement);
          }
          
          setDesign(updatedDesign);
          setSelectedElement(newElement.id); // Seleccionar automáticamente
        };
        img.src = e.target?.result as string;
      };
      reader.readAsDataURL(file);
    }
  };

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    const files = e.dataTransfer.files;
    if (files.length > 0) {
      const file = files[0];
      if (file.type.startsWith('image/')) {
        const reader = new FileReader();
        reader.onload = (e) => {
          if (!design) return;
          
          const canvasSize = getCanvasSize();
          const maxWidth = canvasSize.width - 40;
          const maxHeight = canvasSize.height - 40;
          
          const img = new Image();
          img.onload = () => {
            let newWidth = img.width;
            let newHeight = img.height;
            
            const scaleX = maxWidth / img.width;
            const scaleY = maxHeight / img.height;
            const scale = Math.min(scaleX, scaleY, 2);
            
            newWidth = img.width * scale;
            newHeight = img.height * scale;
            
            const x = (canvasSize.width - newWidth) / 2;
            const y = (canvasSize.height - newHeight) / 2;
            
            const newElement: DesignElement = {
              id: `image-${Date.now()}`,
              type: 'image',
              content: e.target?.result as string,
              position: { x: Math.max(20, x), y: Math.max(20, y) },
              size: { width: newWidth, height: newHeight },
              rotation: 0
            };

            const updatedDesign = { ...design };
            if (currentFace === 'A') {
              updatedDesign.faceA.elements.push(newElement);
            } else if (updatedDesign.faceB) {
              updatedDesign.faceB.elements.push(newElement);
            }
            
            setDesign(updatedDesign);
            setSelectedElement(newElement.id);
          };
          img.src = e.target?.result as string;
        };
        reader.readAsDataURL(file);
      }
    }
  };

  const removeElement = (elementId: string) => {
    if (!design) return;
    
    const updatedDesign = { ...design };
    if (currentFace === 'A') {
      updatedDesign.faceA.elements = updatedDesign.faceA.elements.filter(el => el.id !== elementId);
    } else if (updatedDesign.faceB) {
      updatedDesign.faceB.elements = updatedDesign.faceB.elements.filter(el => el.id !== elementId);
    }
    
    setDesign(updatedDesign);
    setSelectedElement(null);
  };

  const getCurrentFace = (): DesignFace | null => {
    if (!design) return null;
    return currentFace === 'A' ? design.faceA : design.faceB || null;
  };

  const getCanvasSize = () => {
    if (!product || !selectedSize) return { width: 400, height: 200 };
    
    const sizeInfo = product.sizes.find(s => s.label === selectedSize);
    const width = sizeInfo ? sizeInfo.width : 20;
    
    // Calcular dimensiones proporcionales basadas en el tipo de producto y medidas reales
    switch (product.category) {
      case 'lanyard':
        return { width: width * 10, height: 500 }; // Lanyards son largos y estrechos
      case 'pulsera':
        return { width: width * 15, height: 60 }; // Pulseras son anchas y muy cortas
      case 'llavero':
        return { width: width * 6, height: width * 3 }; // Llaveros rectangulares
      case 'cinta':
        return { width: width * 20, height: 40 }; // Cintas muy anchas y delgadas
      case 'cojin':
        return { width: 320, height: 320 }; // Cojines cuadrados 32x32cm (escala 1:10)
      default:
        return { width: 400, height: 200 };
    }
  };

  if (!product) {
    return (
      <div className="min-h-screen bg-white">
        <Header />
        <div className="flex items-center justify-center h-96">
          <div className="text-center">
            <h2 className="font-playfair text-2xl font-bold text-black mb-4">
              Producto no encontrado
            </h2>
            <Link href="/">
              <Button className="ts-button-primary">
                Volver al Catálogo
              </Button>
            </Link>
          </div>
        </div>
      </div>
    );
  }

  const currentFaceData = getCurrentFace();
  const canvasSize = getCanvasSize();

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      
      {/* Product Info Bar */}
      <div className="bg-white border-b border-gray-200 py-4">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <img 
                src={product.image} 
                alt={product.name}
                className="w-12 h-12 rounded-lg object-cover"
              />
              <div>
                <h1 className="font-playfair text-xl font-bold text-black">
                  {product.name}
                </h1>
                <p className="text-sm text-gray-600">
                  Editor 2D - {product.material}
                </p>
              </div>
            </div>
            
            <div className="flex items-center space-x-4">
              <Link href={`/preview?product=${product.id}&design=${design?.id}`}>
                <Button className="ts-button-primary">
                  Vista 3D
                </Button>
              </Link>
              <Link href="/">
                <Button variant="outline" className="ts-button-secondary">
                  Volver
                </Button>
              </Link>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
          
          {/* Left Panel - Tools */}
          <div className="lg:col-span-1">
            <Card>
              <CardHeader>
                <CardTitle className="font-playfair text-lg">
                  Herramientas de Diseño
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-6">
                
                {/* Size Selector */}
                <div>
                  <Label className="text-sm font-medium text-gray-700 mb-2 block">
                    Tamaño del Producto
                  </Label>
                  <Select value={selectedSize} onValueChange={setSelectedSize}>
                    <SelectTrigger>
                      <SelectValue placeholder="Selecciona tamaño" />
                    </SelectTrigger>
                    <SelectContent>
                      {product.sizes.map((size) => (
                        <SelectItem key={size.label} value={size.label}>
                          {size.label}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                {/* Face Selector */}
                {product.faces === 2 && (
                  <div>
                    <Label className="text-sm font-medium text-gray-700 mb-2 block">
                      Cara del Producto
                    </Label>
                    <Tabs value={currentFace} onValueChange={(value) => setCurrentFace(value as 'A' | 'B')}>
                      <TabsList className="grid w-full grid-cols-2">
                        <TabsTrigger value="A">Cara A</TabsTrigger>
                        <TabsTrigger value="B">Cara B</TabsTrigger>
                      </TabsList>
                    </Tabs>
                  </div>
                )}

                {/* Text Tool */}
                <div className="space-y-3">
                  <Label className="text-sm font-medium text-gray-700">
                    Añadir Texto
                  </Label>
                  <Input
                    placeholder="Escribe tu texto..."
                    value={newText}
                    onChange={(e) => setNewText(e.target.value)}
                  />
                  <div className="grid grid-cols-2 gap-2">
                    <div>
                      <Label className="text-xs text-gray-600">Color</Label>
                      <Input
                        type="color"
                        value={textColor}
                        onChange={(e) => setTextColor(e.target.value)}
                        className="h-8"
                      />
                    </div>
                    <div>
                      <Label className="text-xs text-gray-600">Tamaño</Label>
                      <Input
                        type="number"
                        min="8"
                        max="72"
                        value={fontSize}
                        onChange={(e) => setFontSize(Number(e.target.value))}
                        className="h-8"
                      />
                    </div>
                  </div>
                  <Button 
                    onClick={addTextElement}
                    className="w-full ts-button-primary"
                    disabled={!newText.trim()}
                  >
                    Añadir Texto
                  </Button>
                </div>

                {/* Upload Tool */}
                <div className="space-y-3">
                  <Label className="text-sm font-medium text-gray-700">
                    Subir Logo/Imagen
                  </Label>
                  <div 
                    className="border-2 border-dashed border-gray-300 rounded-lg p-4 text-center hover:border-gray-400 transition-colors cursor-pointer"
                    onDragOver={handleDragOver}
                    onDrop={handleDrop}
                    onClick={() => fileInputRef.current?.click()}
                  >
                    <p className="text-sm text-gray-500 mb-2">
                      Arrastra archivos aquí o
                    </p>
                    <Button variant="outline" size="sm" className="ts-button-secondary" type="button">
                      Seleccionar Archivo
                    </Button>
                    <input
                      ref={fileInputRef}
                      type="file"
                      accept="image/*"
                      onChange={handleFileUpload}
                      className="hidden"
                    />
                  </div>
                </div>

                {/* Elements List */}
                {currentFaceData && currentFaceData.elements.length > 0 && (
                  <div className="space-y-3">
                    <Label className="text-sm font-medium text-gray-700">
                      Elementos en Cara {currentFace}
                    </Label>
                    <div className="space-y-2">
                      {currentFaceData.elements.map((element) => (
                        <div 
                          key={element.id}
                          className="flex items-center justify-between p-2 bg-gray-50 rounded"
                        >
                          <span className="text-sm truncate">
                            {element.type === 'text' ? element.content : 'Imagen'}
                          </span>
                          <Button
                            size="sm"
                            variant="ghost"
                            onClick={() => removeElement(element.id)}
                            className="text-red-500 hover:text-red-700"
                          >
                            ✕
                          </Button>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

              </CardContent>
            </Card>
          </div>

          {/* Center Panel - Canvas */}
          <div className="lg:col-span-2">
            <Card>
              <CardHeader>
                <CardTitle className="font-playfair text-lg flex items-center justify-between">
                  <span>Plantilla 2D - Cara {currentFace}</span>
                  <div className="text-sm font-normal text-gray-600">
                    {selectedSize} - {product.category}
                  </div>
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="flex justify-center">
                  <div 
                    className="relative bg-white border-2 border-gray-300 rounded-lg overflow-hidden" 
                    style={{ 
                      width: Math.min(canvasSize.width, 600),
                      height: Math.min(canvasSize.height, 400),
                      backgroundColor: currentFaceData?.backgroundColor || '#FFFFFF'
                    }}
                  >
                    
                    {/* Product Template Outline */}
                    <div className="absolute inset-2 border-2 border-dashed border-gray-400 rounded flex items-center justify-center">
                      {currentFaceData?.elements.length === 0 && (
                        <div className="text-center">
                          <span className="text-gray-400 text-sm block">
                            Área de diseño - {product.name}
                          </span>
                          <span className="text-gray-400 text-xs">
                            {selectedSize}
                          </span>
                        </div>
                      )}
                    </div>

                    {/* Design Elements */}
                    {currentFaceData?.elements.map((element) => (
                      <div
                        key={element.id}
                        className={`absolute cursor-pointer border-2 ${
                          selectedElement === element.id ? 'border-blue-500' : 'border-transparent'
                        } hover:border-blue-300`}
                        style={{
                          left: `${element.position.x}px`,
                          top: `${element.position.y}px`,
                          width: `${element.size.width}px`,
                          height: `${element.size.height}px`,
                          transform: `rotate(${element.rotation}deg)`
                        }}
                        onClick={() => setSelectedElement(element.id)}
                      >
                        {element.type === 'text' && (
                          <div
                            style={{
                              color: element.color,
                              fontSize: `${element.fontSize}px`,
                              fontFamily: element.fontFamily,
                              width: '100%',
                              height: '100%',
                              display: 'flex',
                              alignItems: 'center',
                              justifyContent: 'center'
                            }}
                          >
                            {element.content}
                          </div>
                        )}
                        {element.type === 'image' && (
                          <img 
                            src={element.content} 
                            alt="Diseño" 
                            className="w-full h-full object-contain"
                            draggable={false}
                          />
                        )}
                      </div>
                    ))}
                  </div>
                </div>

                {/* Canvas Controls */}
                <div className="flex justify-center mt-4 space-x-4">
                  <Button variant="outline" size="sm" className="ts-button-secondary">
                    Zoom -
                  </Button>
                  <Button variant="outline" size="sm" className="ts-button-secondary">
                    Centrar
                  </Button>
                  <Button variant="outline" size="sm" className="ts-button-secondary">
                    Zoom +
                  </Button>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Right Panel - Product Info */}
          <div className="lg:col-span-1">
            <Card>
              <CardHeader>
                <CardTitle className="font-playfair text-lg">
                  Información del Producto
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                
                <div>
                  <Label className="text-sm font-medium text-gray-700">Material</Label>
                  <p className="text-sm text-gray-900">{product.material}</p>
                </div>

                <div>
                  <Label className="text-sm font-medium text-gray-700">Tamaño Seleccionado</Label>
                  <p className="text-sm text-gray-900">{selectedSize}</p>
                </div>

                <div>
                  <Label className="text-sm font-medium text-gray-700">Caras</Label>
                  <p className="text-sm text-gray-900">
                    {product.faces === 1 ? '1 Cara' : '2 Caras'}
                  </p>
                </div>

                <div>
                  <Label className="text-sm font-medium text-gray-700">Extras Disponibles</Label>
                  <p className="text-sm text-gray-600">
                    {product.extras.length} opciones disponibles
                  </p>
                </div>

                <div className="pt-4 border-t">
                  <Button className="w-full ts-button-primary mb-2">
                    Guardar Diseño
                  </Button>
                  <Button variant="outline" className="w-full ts-button-secondary">
                    Plantillas Predefinidas
                  </Button>
                </div>

              </CardContent>
            </Card>
          </div>

        </div>
      </div>
    </div>
  );
}

export default function EditorPage() {
  return (
    <Suspense fallback={
      <div className="min-h-screen bg-white">
        <Header />
        <div className="flex items-center justify-center h-96">
          <div className="text-center">
            <div className="w-8 h-8 border-4 border-blue-500 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
            <p className="text-gray-600">Cargando editor...</p>
          </div>
        </div>
      </div>
    }>
      <EditorContent />
    </Suspense>
  );
}
